import React from 'react'
import styled from 'styled-components'

export default function Mcontact() {
  return (
    <Container>
      <div className='container-m' id="contact">
        <div className='heading'>
          <h2>Contact Us</h2>
        </div>
        <div className='contactInfo'>
          <div className='boxshadow'>
            <div className='box'>
              <div className='text'>
                <div className='icon'>
                  {/* <i className="fas fa-map-marker-alt	"></i> */}
                  <h2>Address</h2>
                </div>
                <p>
                  7TH Mile Stone ,Giani Border,Metro Pillar No. 152
                  <br />
                  Chikamberpur, Sahibabad, Ghaziabad-201006
                </p>

                <p>
                  Corp. Office-K-61,A Patel NAgar 3rd,
                  <br />
                  Ghaziabad-201001
                </p>

                <p>
                  B.O.: Oriental Tiles Ke Samne Gopalpur Telmil,
                  <br /> Sikandrabad
                </p>
              </div>
            </div>
            <div className='box'>
              <div className='text'>
                <div className='icon'>
                  {/* <i class='fas fa-phone-alt'></i> */}
                  <h2>Contact No.</h2>
                </div>
                <p>9910415039</p>
              </div>
            </div>
            <div className='box'>
              <div className='text'>
                <div className='buttonemail'>
                  <a href='mailto:gparivahan07@gmail.com'>
                    <input
                      className='email'
                      type='button'
                      value='Enquiry Through Mail'
                    />
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Container>
  )
}
const Container = styled.div`
  .heading {
    width: 100%;
    height: 13vh;
    display: flex;
    font-size: 26px;
    justify-content: center;
    color: #2dd465;
  }
  .heading ::after {
    content: '';
    height: 6px;
    width: 187px;
    background-color: #2dd465;
    border-radius: 25px;
    display: block;
    margin: auto;
  }
  .contactInfo {
    text-align: center;
    height: 62vh;
    font-size: 19px;
    display: flex;
    justify-content: center;
  }
  .boxshadow {
    width: 75%;
    height: auto;
    box-shadow: 0 5px 15px 0 rgb(0, 0, 0, 0.25);
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 12px;
    padding: 20px;
  }
  .contactInfo .box .icon {
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 22px;
  }
  .contactInfo .box .icon i {
    margin-right: 8px;
    color: rgba(7, 49, 38, 0.75);
  }
  .contactInfo .box .text p {
    color: rgba(7, 49, 38, 0.75);
    letter-spacing: 2.5px;
  }
  .contactInfo .box .text h2 {
    color: rgba(7, 49, 38, 0.75);
    letter-spacing: 2.5px;
  }
  .buttonemail {
    width: 100%;
  }
  .buttonemail .email {
    width: 24%;
    padding: 6px 2px;
    margin-top: 11px;
    color: white;
    background: #2dd465;
    border: 4px solid rgb(0 150 111 / 75%);
    border-radius: 12px;
  }
  @media screen and (max-width: 1045px) {
    .heading {
      height: 89px;
    }
    .contactInfo {
      height: auto;
    }
    .boxshadow {
      height: auto;
    }
  }
  @media screen and (max-width: 785px) {
    .buttonemail .email {
      width: 114px;
      font-size: 10px;
    }
    .boxshadow {
      width: 90%;
    }
    .container-m {
      margin-top: 100px;
    }
  }
  @media screen and (max-width: 475px) {
    .container-m {
      margin-top: 184px;
    }
  }
  @media screen and (max-width: 400px) {
    .container-m {
      margin-top: 241px;
    }
  }
  @media screen and (max-width: 374px) {
    .container-m {
      margin-top: 408px;
    }
  }
  @media screen and (max-width: 335px) {
    .container-m {
      margin-top: 501px;
    }
  }
  // @media screen and (max-width: 785px) {
  //   .buttonemail .email {
  //     width: 29%;
  //   }
  // }
  // @media screen and (max-width: 659px) {
  //   .buttonemail .email {
  //     width: 33%;
  //   }
  // }
  // @media screen and (max-width: 569px) {
  //   .buttonemail .email {
  //     font-size: 12px;
  //   }
  // }
  // @media screen and (max-width: 529px) {
  //   .buttonemail .email {
  //     width: 133px;
  //   }
  // }
  // @media screen and (max-width: 400px) {
  //   .buttonemail .email {
  //     width: 119px;
  //     font-size: 11px;
  //   }
  // }
  // @media screen and (max-width: 572px) {
  //   .contactInfo .box .text p {
  //     letter-spacing: 1.5px;
  //   }
  //   .contactInfo .box .text h2 {
  //     letter-spacing: 1.5px;
  //   }
  // }
  // @media screen and (max-width: 381px) {
  //   .boxshadow {
  //     height: 64vh;
  //   }
  // }
`
