import React, { useEffect, useState } from 'react'
import axios from 'axios'
import logo from '../image/logo.png'
import { Link } from 'react-scroll'
import { ToastContainer,toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
function Nav() {
  const [width, setWidth] = useState(window.innerWidth)
  const [shownav, setshownav] = useState(false)
  const [showForma, setShowForma] = useState(false)
  const [scrollData, setScrollData] = useState({
    y: 0,
    lastY: 0,
  })
  const [hideNav, setHideNav] = useState(false)
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [mobile, setMobile] = useState('')
  const notify = () => {
    toast.success("Thanks for submitting! We will be contact you within 1-2hr. Call +91-99104-15039 for help.", {
      position: toast.POSITION.TOP_CENTER,
      autoClose: 8000
    });
  }
  const sendEmail = async (data) => {
    try {
      const response = await axios.post('/sendmail', {
        data,
      })
      notify()
      setShowForma(false)
      setName("")
    } catch (error) {
      console.log(error)
    }
  }
  useEffect(() => {
    const handleScroll = () => {
      setScrollData((lastState) => {
        return {
          y: window.scrollY,
          lastY: lastState.y,
        }
      })
    }
    window.addEventListener('scroll', handleScroll)
    return () => {
      window.removeEventListener('scroll', handleScroll)
    }
  }, [])
  useEffect(() => {
    if (scrollData.y > 350 && scrollData.y - scrollData.lastY > 0) {
      setHideNav(true)
    } else {
      setHideNav(false)
    }
  }, [scrollData])
  useEffect(() => {
    window.addEventListener('resize', () => {
      setWidth(window.innerWidth)
    })
    return () => {
      window.removeEventListener('resize', () => {})
    }
  })
  return (
    <>
      {width < 720 && (
        <div className='nav'>
          <img src={logo} className='Logo' alt='' />
          <img
            src='https://img.icons8.com/external-febrian-hidayat-basic-outline-febrian-hidayat/24/null/external-menu-bar-ui-essential-febrian-hidayat-basic-outline-febrian-hidayat.png'
            onClick={() => setshownav(!shownav)}
            alt=''
            className='menubar'
          />
        </div>
      )}
      {width > 720 && (
        <div className={hideNav ? 'hidden' : 'nav1'}>
          <img src={logo} className='Logo' alt='' />
          {showForma&&(
          <form className='orderforms'>
            <p className='close' onClick={() => setShowForma(!showForma)}>
              <i class='fa fa-times' aria-hidden='true'></i>
            </p>
            <div className='field fields'>
              <input
                className='padding'
                type='name'
                placeholder='Enter Name'
                required
                onChange={(e) => {
                  setName(e.target.value)
                }}
              ></input>
            </div>
            <div className='field fields'>
              <input
                className='padding'
                type='email'
                placeholder='Enter Email (optional)'
                required
                onChange={(e) => {
                  setEmail(e.target.value)
                }}
              ></input>
            </div>
            <div className='field fields'>
              <input
                className='padding'
                placeholder='Enter Phone no.'
                required
                onChange={(e) => {
                  setMobile(e.target.value)
                }}
              ></input>
            </div>
            <div className='field fields' onClick={() => setShowForma(!showForma)}>
              <input type='submit' className='button' value='Send' onClick={(e) => {
                e.preventDefault()
                sendEmail({
                  name,
                  email,
                  mobile,
                  emailType: 'truck owner email',
                })
              }}></input>
            </div>
            <ToastContainer/>
          </form>
          )}
          {width > 720 && (
            <div className='navBar'>
              <Link to='home'>
                <a href='http://'>Home</a>
              </Link>
              <Link to='about'>
                <a href='http://'>About us</a>
              </Link>
              <Link to='services'>
                <a href='http://'>Services</a>
              </Link>
              <Link to='contact'>
                <a href='http://'>Contact us</a>
              </Link>
              <Link to='faQ'>
                <a href='http://'>FAQ</a>
              </Link>
              <button
                className='iamOwner'
                onClick={() => setShowForma(!showForma)}
              >
                Im a truck owner
              </button>
            </div>
          )}
        </div>
      )}
      {width < 720 && shownav && <Mobilenav shownav setshownav={setshownav} />}
    </>
  )
}

const Mobilenav = ({ shownav, setshownav }) => {
  const [showForml, setShowForml] = useState(false)
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [mobile, setMobile] = useState('')
  const notify = () => {
    toast.success("Thanks for submitting! We will be contact you with in 1-2hr. Call +91-99104-15039 for help.", {
      position: toast.POSITION.TOP_CENTER,
      autoClose: 8000
    });
  }
  const sendEmail = async (data) => {
    try {
      const response = await axios.post('/sendmail', {
        data,
      })
      notify()
      setShowForml(false)
      setshownav(false)
    } catch (error) {
      console.log(error)
    }
  }
  
  return (
    <>{showForml&&(
      <form className='orderformss'>
        <p className='close' onClick={() => setShowForml(!showForml)}>
          <i class='fa fa-times' aria-hidden='true'></i>
        </p>
        <div className='field fields'>
          <input
            className='padding'
            type='name'
            placeholder='Enter Name'
            required
            onChange={(e) => {
              setName(e.target.value)
            }}
          ></input>
        </div>
        <div className='field fields'>
          <input
            className='padding'
            type='email'
            placeholder='Enter Email (optional)'
            onChange={(e) => {
              setEmail(e.target.value)
            }}
          ></input>
        </div>
        <div className='field fields'>
          <input
            className='padding'
            placeholder='Enter Phone no.'
            required
            onChange={(e) => {
              setMobile(e.target.value)
            }}
          ></input>
        </div>
        <div className='field fields'>
          <input type='submit' className='button' value='Send' onClick={(e) => {
            e.preventDefault()
            sendEmail({
              name,
              email,
              mobile,
              emailType: 'truck owner email',
            })
          }}></input>
        </div>
      </form>
      )}
      <div className='MainMobilenavParent'>
        <div className='mobilenavchild'>
          <Link to='home'>
            <a
              onClick={() => setshownav(!shownav)}
              href='http://'
              style={{ textDecoration: 'none', color: '#2dd465' }}
            >
              Home
            </a>
          </Link>
        </div>
        <div className='mobilenavchild'>
          <Link to='about'>
            <a
              onClick={() => setshownav(!shownav)}
              href='http://'
              style={{ textDecoration: 'none', color: '#2dd465' }}
            >
              About us
            </a>
          </Link>
        </div>
        <div className='mobilenavchild'>
          <Link to='services'>
            <a
              onClick={() => setshownav(!shownav)}
              href='http://'
              style={{ textDecoration: 'none', color: '#2dd465' }}
            >
              Services
            </a>
          </Link>
        </div>
        <div className='mobilenavchild'>
        <Link to='contact'>
            <a
              onClick={() => setshownav(!shownav)}
              href='http://'
              style={{ textDecoration: 'none', color: '#2dd465' }}
            >
              Contact us
            </a>
          </Link>
          </div>
        <div className='mobilenavchild'>
          <Link to='faQ'>
            <a
              onClick={() => setshownav(!shownav)}
              href='http://'
              style={{ textDecoration: 'none', color: '#2dd465' }}
            >
              FAQs
            </a>
          </Link>
        </div>
        <div className='mobilenavchild'>
          <button className='iamOwner' onClick={() => setShowForml(!showForml)}>
            Im a truck owner
          </button>
        </div>
      </div>
    </>
  )
}

export default Nav
