import React from 'react'
import truck from '../image/transport1.jpg'
function Insourance() {
  return (
    <div className='Insourancefeatures'>
      <div className='InsourancefeaturesContainer'>
        <img src={truck} alt='hi' />
        <div className='insouranceconatiner'>
          <h1> Gayatri Parivahan offering Instant Cargo Insurance</h1>
          <p className='small'>
            Now get Transit insurance at the lowest price and hassle-free claim
            process with Trukky insurance powered by Bajaj Finserv.
          </p>
        </div>
      </div>
    </div>
  )
}

export default Insourance
