import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { ToastContainer, toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
let citydata

function Searchdiv() {
  const [startcitytext, setstartcitytext] = useState('')
  const [endcitytext, setendcitytext] = useState('')
  const [startcityresult, setStartcityresult] = useState([])
  const [startcityfinal, setStartcityfinal] = useState()
  const [Endcityresult, setEndcityresult] = useState([])
  const [Endcityfinal, setEndcityfinal] = useState()
  const [showForm, setShowForm] = useState(false)
  const [email, setemail] = useState('')
  const [mobile, setMobile] = useState(0)
  const [weight, setWeight] = useState('')
  const [weightType, setWeightType] = useState('')
  const [name, setName] = useState('')

  const notify = () => {
    toast.success(
      'Thanks for submitting! We will be contact you with in 1-2hr. Call +91-99104-15039 for help.',
      {
        position: toast.POSITION.TOP_CENTER,
        autoClose: 8000,
      }
    )
  }
  const sendEmail = async (data) => {
    try {
      const response = await axios.post('/sendmail', {
        data,
      })
      notify()
      setShowForm(false)
      // console.log(response)
      setstartcitytext('')
      setendcitytext('')
      setEndcityfinal('')
      setStartcityfinal('')
    } catch (error) {
      console.log(error)
    }
  }

  const getdata = async () => {
    try {
      const tempcitydata = await fetch(
        'https://raw.githubusercontent.com/nshntarora/Indian-Cities-JSON/master/cities.json'
      )
      citydata = await tempcitydata.json()
    } catch (error) {
      console.log(error)
    }
  }
  const searchEngionstart = (query) => {
    citydata.map((item) => {
      const key = item['name'].toLowerCase()
      if (key.startsWith(query.toLowerCase())) {
        if (startcityresult.length < 9) {
          if (!startcityresult.includes(item)) {
            setStartcityresult([...startcityresult, item])
          } else {
            setStartcityresult([...startcityresult])
          }
        }
      }
      return item
    })
  }
  const searchEngionend = (query) => {
    citydata.map((item) => {
      const key = item['name'].toLowerCase()
      if (key.startsWith(query.toLowerCase())) {
        if (Endcityresult.length < 9) {
          if (!Endcityresult.includes(item)) {
            setEndcityresult([...Endcityresult, item])
          } else {
            setEndcityresult([...Endcityresult])
          }
        }
      }
      return item
    })
  }
  useEffect(() => {
    getdata()
  }, [])
  useEffect(() => {
    setStartcityresult([])
    if (startcitytext) {
      searchEngionstart(startcitytext)
    }
  }, [startcitytext])

  useEffect(() => {
    setEndcityresult([])
    if (endcitytext) {
      searchEngionend(endcitytext)
    }
  }, [endcitytext])
  return (
    <>
      <div className='searchBox'>
        <div className='outerinput'>
          <div className='locationInput'>
            <p>Enter pickup city</p>
            <input
              type='text'
              placeholder='Enter pickup city'
              onChange={(e) => {
                setstartcitytext(e.target.value)
                setStartcityfinal('')
              }}
              value={
                typeof startcityfinal === 'object'
                  ? `${startcityfinal.name},${startcityfinal.state}`
                  : startcitytext
              }
            />
            {!startcityfinal && (
              <div className='searchfirstcityresult'>
                {startcityresult
                  .slice(0)
                  .reverse()
                  .map((item) => {
                    return (
                      <div
                        className='searchresult'
                        onClick={() => setStartcityfinal(item)}
                      >
                        <h1>{item.name}</h1>
                        <p>{item.state}</p>
                      </div>
                    )
                  })}
              </div>
            )}
          </div>
          <div className='locationInput'>
            <p>Enter drop city</p>
            <input
              type='text'
              placeholder='Enter drop city'
              onChange={(e) => {
                setendcitytext(e.target.value)
                setEndcityfinal('')
              }}
              value={
                typeof Endcityfinal === 'object'
                  ? `${Endcityfinal.name},${Endcityfinal.state}`
                  : endcitytext
              }
            />
            {!Endcityfinal && (
              <div className='searchfirstcityresult'>
                {Endcityresult.slice(0)
                  .reverse()
                  .map((item) => {
                    return (
                      <div
                        className='searchresult'
                        onClick={() => setEndcityfinal(item)}
                      >
                        <h1>{item.name}</h1>
                        <p>{item.state}</p>
                      </div>
                    )
                  })}
              </div>
            )}
          </div>
        </div>
        <div
          className='buttonsearch'
          onClick={() => {
            setShowForm(!showForm)
            if (!Endcityfinal) setEndcityfinal(endcitytext)
            if (!startcityfinal) setStartcityfinal(startcitytext)
          }}
        >
          <p>Check price</p>
        </div>
      </div>
      {showForm && startcityfinal && Endcityfinal && (
        <form className='orderform'>
          <p className='close' onClick={() => setShowForm(!showForm)}>
            <i class='fa fa-times' aria-hidden='true'></i>
          </p>
          <div className='field'>
            <input
              className='padding'
              type='name'
              placeholder='Enter Name'
              required
              onChange={(e) => {
                setName(e.target.value)
              }}
            ></input>
          </div>
          <div className='field'>
            <input
              required
              className='padding'
              type='email'
              placeholder='Enter Email'
              onChange={(e) => setemail(e.target.value)}
            ></input>
          </div>
          <div className='field'>
            <input
              className='padding'
              placeholder='Enter Phone no.'
              required
              onChange={(e) => setMobile(e.target.value)}
            ></input>
          </div>
          <div className='field'>
            <input
              className='padding'
              placeholder='Enter Weight'
              required
              onChange={(e) => setWeight(e.target.value)}
            ></input>
          </div>
          <div className='radios'>
            <input
              type='radio'
              name='myRadio'
              id='myRadio1'
              value='Kg'
              className='radio__input'
              onChange={(e) => setWeightType(e.target.value)}
            />
            <label className='radio__label' for='myRadio1'>
              Kg
            </label>
            <input
              type='radio'
              name='myRadio'
              id='myRadio2'
              value='Ton'
              className='radio__input'
              onChange={(e) => setWeightType(e.target.value)}
            />
            <label className='radio__label' for='myRadio2'>
              Ton
            </label>
          </div>
          <div className='field'>
            <input
              type='submit'
              className='button'
              value='Send'
              onClick={(e) => {
                e.preventDefault()
                sendEmail({
                  name,
                  mobile,
                  email,
                  weight,
                  weightType,
                  'pickup-city': startcityfinal,
                  'Destination-city': Endcityfinal,
                  emailType: 'order',
                })
              }}
            ></input>
          </div>
        </form>
      )}
      <ToastContainer />
    </>
  )
}

export default Searchdiv
