import React, { useState } from 'react'
import { faq } from '../data/faq'
function Faq() {
  return (
    <div className='faq' id="faQ">
      <h1>FAQs</h1>
      <div className='faqcontainer'>
        {faq.map((item, id) => {
          return <Qna key={id} {...item} />
        })}
      </div>
    </div>
  )
}
const Qna = ({ question, answer, id }) => {
  const [showans, setshowans] = useState(false)
  return (
    <>
      <div className='outerqna'>
        <div className='question'>
          <p className='questiontext'>
            {`Q ${id}`}&nbsp;&nbsp;
            {question}
          </p>
          <p style={{ cursor: 'pointer' }} onClick={() => setshowans(!showans)}>
            
          </p>
        </div>
        {showans && <div className='answer'>{answer}</div>}
      </div>
    </>
  )
}

export default Faq
