import './App.css'
// import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import { useEffect, useState } from 'react'
import MainBody from './component/MainBody'
import SliderComponent from './component/slider'
import About from './component/About'
import Services from './component/Services'
import Contact from './component/Contact'
import Nav from './component/nav'
import Features from './component/Features'
import Insourance from './component/Insourance'
import Statics from './component/Statics'
import Faq from './component/Faq'
import Mcontact from './component/Mcontact'

function App() {
  const [width, setWidth] = useState(window.innerWidth)
  useEffect(() => {
    window.addEventListener('resize', () => {
      setWidth(window.innerWidth)
    })
    return () => {
      window.removeEventListener('resize', () => {})
    }
  })
  return (
    <>
      <div className='CONTAINER'>
        <Nav />
        <div className='outerdiv'>
          <MainBody />
        </div>
        <Features />
        <SliderComponent />
        <Insourance />
        <Statics />
        <About />
        <div className='spaceAS'></div>
        <Services />
        <div className='space'></div>
        {width < 1045 ? <Mcontact /> : <Contact />}
        <Faq />
        <div className='space'></div>
        <footer>
          <h3> gayatri-parivahan.com, All rights reserved</h3>
        </footer>
      </div>
    </>
  )
}

export default App
