import React, { useState } from 'react'
import axios from 'axios'
import styled from 'styled-components'
import image from '../image/istockphoto-1208592742-612x612.jpg'
import { ToastContainer, toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
export default function Contact() {
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [msg, setmsg] = useState('')
  const [mobile, setMobile] = useState('')

  const notify = () => {
    toast.success(
      'Thanks for submitting! We will be contact you with in 1-2hr. Call +91-99104-15039 for help.',
      {
        position: toast.POSITION.TOP_CENTER,
        autoClose: 8000,
      }
    )
  }
  const sendEmail = async (data) => {
    try {
      const response = await axios.post('/sendmail', {
        data,
      })
      setName('')
      setEmail('')
      setmsg('')
      setMobile('')
      notify()
    } catch (error) {
      console.log(error)
    }
  }
  return (
    <Container>
      <div className='spaceSC'></div>
      <div className='image' id='contact'>
        <img src={image} alt='' />
        <div className='container'>
          <div className='heading'>
            <h2>Contact Us</h2>
          </div>
          <div className='divide'>
            <div className='contactInfo'>
              <div className='box'>
                <div className='text'>
                  <div className='icon'>
                    <i className='fas fa-map-marker-alt	'></i>
                    <h2>Address</h2>
                  </div>
                  <p>
                    7TH Mile Stone ,Giani Border,Metro Pillar No. 152
                    <br />
                    Chikamberpur, Sahibabad, Ghaziabad-201006
                  </p>

                  <p>
                    Corp. Office-K-61,A Patel NAgar 3rd,
                    <br />
                    Ghaziabad-201001
                  </p>

                  <p>
                    B.O.: Oriental Tiles Ke Samne Gopalpur Telmil,
                    <br /> Sikandrabad
                  </p>
                </div>
              </div>
              <div className='box'>
                <div className='text'>
                  <div className='icon'>
                    <i class='fas fa-phone-alt'></i>
                    <h2>Contact No.</h2>
                  </div>
                  <p>9910415039</p>
                </div>
              </div>
              <div className='box'>
                <div className='text'>
                  <div className='icon'>
                    <i class='fas fa-envelope'></i>
                    <h2>Email</h2>
                  </div>
                  <p>gparivahan07@gmail.com</p>
                </div>
              </div>
            </div>
            <div className='containers'>
              <div className='form-container'>
                <div className='slide-tag'>
                  <label htmlFor='signup' className='slide signup'>
                    Send Message
                  </label>
                </div>
                <div className='form-inner'>
                  <form className='signup'>
                    <div className='field'>
                      <span>
                        <i className='fas fa-user ic'></i>
                      </span>
                      <input
                        className='padding'
                        type='name'
                        placeholder='Enter your Name'
                        value={name}
                        required
                        onChange={(e) => {
                          setName(e.target.value)
                        }}
                      ></input>
                    </div>
                    <div className='field'>
                      <span>
                        <i className='fas fa-at ic'></i>
                      </span>
                      <input
                        className='padding'
                        type='email'
                        placeholder='Enter Email'
                        required
                        value={email}
                        onChange={(e) => {
                          setEmail(e.target.value)
                        }}
                      ></input>
                    </div>
                    <div className='field'>
                      <span>
                        <i className='fas fa-phone-alt'></i>
                      </span>
                      <input
                        className='padding'
                        type='text'
                        placeholder='Enter Phone no.'
                        required
                        value={mobile}
                        onChange={(e) => {
                          setMobile(e.target.value)
                        }}
                      ></input>
                    </div>
                    <div className='field'>
                      <span>
                        <i className='fas fa-comment'></i>
                      </span>
                      <input
                        className='padding'
                        placeholder='Type your Message...'
                        required
                        value={msg}
                        onChange={(e) => {
                          setmsg(e.target.value)
                        }}
                      ></input>
                    </div>
                    <div
                      className='field'
                      onClick={(e) => {
                        e.preventDefault()
                        sendEmail({
                          name,
                          email,
                          mobile,
                          msg,
                          emailType: 'Enquiry email',
                        })
                      }}
                    >
                      <input
                        type='submit'
                        className='button'
                        value='Send'
                      ></input>
                    </div>
                  </form>
                  <ToastContainer />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Container>
  )
}
const Container = styled.div`
  img {
    width: 100%;
    height: 100vh;
    background: center;
    background-size: cover;
    background-repeat: no-repeat;
    overflow-x: hidden;
    overflow-y: auto;
  }
  .image {
    position: relative;
  }
  .container {
    height: 78vh;
    width: 100%;
    position: absolute;
    bottom: 90px;
  }
  .heading {
    width: 100%;
    height: 13vh;
    display: flex;
    font-size: 26px;
    justify-content: center;
    color: aliceblue;
  }
  .container .heading h2::after {
    content: '';
    height: 6px;
    width: 187px;
    background-color: aliceblue;
    border-radius: 25px;
    display: block;
    margin: auto;
  }
  .divide {
    display: flex;
    display: flex;
    text-align: center;
    justify-content: space-around;
  }
  .contactInfo {
    text-align: center;
    height: 62vh;
    margin-top: 20px;
    font-size: 19px;
  }
  .contactInfo .box .icon {
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 22px;
  }
  .contactInfo .box .icon i {
    margin-right: 8px;
    color: #16413bab;
  }
  .contactInfo .box .text p {
    color: #1a7e6eab;
  }
  .contactInfo .box .text h2 {
    color: #16413bab;
  }
  .contactForm {
    width: 40%;
    padding: 40px;
    background: #fff;
  }
  .containers {
    overflow: hidden;
    margin-top: 23px;
    height: 48vh;
    width: 335px;
    background: white;
    text-align: center;
    border-radius: 5px;
  }
  .form-container .form-inner form .field {
    height: 28px;
    width: 100%;
    margin-top: 23px;
  }
  .form-inner form .field input {
    height: 100%;
    width: 75%;
    outline: none;
    font-size: 18px;
    border-radius: 5px;
    color: #1a7e6eab;
    border: 1px solid #82b6b2;
    border-bottom-width: 3px;
    transition: all 0.4s ease;
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3) inset;
  }
  .padding {
    padding-left: 20px;
  }
  input::-webkit-input-placeholder {
    color: #1a7e6eab;
  }
  .form-inner form .field input:focus {
    border-color: #457470;
  }
  form .field input[type='submit'] {
    color: #fff;
    width: 98px;
    font-size: 17px;
    font-weight: 50;
    border: none;
    cursor: pointer;
    box-shadow: none;
    background: #58b1a9;
  }
  #input {
    padding: 0px 0px;
  }
  form .field input[type='submit']:hover {
    font-size: 21px;
    letter-spacing: 1px;
  }
  .form-container .form-inner {
    display: flex;
    width: 200%;
  }
  .form-container .form-inner form {
    width: 50%;
    transition: all 0.6s cubic-bezier(0.68, -0.55, 0.565, 1.55);
  }
  .container .form-container {
    width: 100%;
    overflow: hidden;
  }
  .form-container .slide-tag {
    position: relative;
    display: flex;
    height: 50px;
    width: 100%;
    overflow: hidden;
    border-radius: 5px;
    margin: 30px 0 10px 0;
    justify-content: space-between;
    /* border: 1px solid lightgray; */
  }
  .slide-tag .slide {
    height: 100%;
    width: 100%;
    font-size: 18px;
    font-weight: 500;
    line-height: 48px;
  }
  .slide-tag .signup {
    color: #16413bab;
    font-size: 32px;
  }
  span {
    position: absolute;
    margin: 4px 4px;
    color: #2f7a66db;
  }
`
